import { useCallback, useMemo, useState } from "react";
import { CancelIcon } from "../icons/cancel";
import "./form.css";
import { useStore } from "../store";
import { useShallow } from "zustand/react/shallow";

export const Form = () => {
  const [roads, isPlaying, wrongGuesses, startGame, cancelGame, submitGuess] =
    useStore(
      useShallow((state) => [
        state.roads,
        state.isPlaying,
        state.wrongGuesses,
        state.startGame,
        state.cancelGame,
        state.submitGuess,
      ]),
    );

  const score = useMemo(
    () => ({
      correct: roads.filter((r) => r.state === "correct").length,
      wrong: wrongGuesses,
      total: roads.length,
    }),
    [roads, wrongGuesses],
  );

  const [isCorrect, setIsCorrect] = useState(false);
  const [isWrong, setIsWrong] = useState(false);

  const onKeyDown = useCallback(
    (event) => {
      if (event.keyCode === 13) {
        const result = submitGuess(event.target.value);
        const fn = result ? setIsCorrect : setIsWrong;
        fn(true);
        setTimeout(() => fn(false), 1000);
        event.target.value = "";
      }
    },
    [setIsCorrect, setIsWrong, submitGuess],
  );

  return (
    <div
      style={{
        zIndex: 1000000,
        position: "absolute",
        bottom: "50px",
        right: "50px",
        left: "50px",
      }}
    >
      {isPlaying ? (
        <div>
          <p className="has-text-centered mb-3">
            {score.correct} / {score.total}
            {score.wrong ? ` (${score.wrong} wrong)` : ""}
          </p>
          <div className="container" style={{ display: "flex", gap: "8px" }}>
            <input
              type="text"
              placeholder="Enter street name"
              onKeyDown={onKeyDown}
              autoFocus
              className={`input text-input ${isCorrect ? "correct" : ""} ${isWrong ? "wrong" : ""}`}
            />
            <button className="button" onClick={cancelGame}>
              <span className="icon is-small">
                <CancelIcon color="#fff" />
              </span>
            </button>
          </div>
        </div>
      ) : (
        <button className="button is-fullwidth" onClick={startGame}>
          Start
        </button>
      )}
    </div>
  );
};
