import { Polygon } from "react-leaflet";
import { useCallback, useEffect, useMemo, useRef } from "react";
import { useStore } from "../store.ts";
import { Layer, LayersControlEventHandlerFn } from "leaflet";
import { useShallow } from "zustand/react/shallow";

export const Selection = () => {
  const [setSelection, isPlaying] = useStore(
    useShallow((state) => [state.setSelection, state.isPlaying]),
  );

  const ref = useRef(null);

  useEffect(() => {
    ref?.current?.enableEdit();
  }, [ref]);

  useEffect(() => {
    if (isPlaying) {
      ref.current?.disableEdit();
      ref.current?.setStyle({ opacity: 0 });
    } else {
      ref.current?.enableEdit();
      ref.current?.setStyle({ opacity: 1 });
    }
  }, [isPlaying]);

  const onSelectionUpdate = useCallback(
    (layer: Layer) =>
      setSelection(
        layer
          .toGeoJSON()
          .geometry.coordinates[0].map((point) => [point[1], point[0]]),
      ),
    [setSelection],
  );

  const eventHandlers = useMemo(
    (): Record<string, LayersControlEventHandlerFn> => ({
      "editable:edited": (e) => onSelectionUpdate(e.layer),
      "editable:vertex:deleted": (e) => onSelectionUpdate(e.layer),
    }),
    [onSelectionUpdate],
  );

  return (
    <Polygon
      ref={ref}
      eventHandlers={eventHandlers}
      editable
      fill={false}
      positions={[
        [47.831365, 16.430752],
        [47.825229, 16.412673],
        [47.83874, 16.398811],
        [47.845135, 16.405416],
        [47.831365, 16.430752],
      ]}
    />
  );
};
